import Vue from 'vue'
import loginpage from './loginpage.vue'
import loadings from '@/components/loading/loading.vue'
import router from './router'
import VueI18n from 'vue-i18n'
import global_ from '@/assets/js/OJInformation.js'
import messages from '@/assets/js/OJFunction.js'
import '@/assets/js/include.js'
Vue.config.productionTip = false
Vue.prototype.GLOBAL = global_
Vue.use(VueI18n)

const i18n = new VueI18n({
    locale:  global_.OJ_DEFAULT_LANG,
    messages: messages.messages
});

new Vue({
    i18n,
    router,
    render: h => h(loginpage)
}).$mount('#loginpage')

new Vue({
    i18n,
    router,
    render: h => h(loadings)
}).$mount('#loadings')