<template>
    <div id="loginpage">
        <nav-bar :isActive="page_setting.belong"></nav-bar>
        <div class="container mundb-standard-container">
            <div class="row justify-content-sm-center">
                <div class="col-sm-12 col-md-8 col-lg-6">
                    <div class="text-center" style="margin-top:10vh;margin-bottom:20px;">
                        <h1 style="padding:20px;display:inline-block;">CAOJ</h1>
                        <p>CAOJ's yet another Online Judge</p>
                    </div>
                    <div class="card">
                        <div class="card-header">
                            <ul class="nav nav-tabs card-header-tabs nav-justified nav-tabs-material" id="accountTab"
                                role="tablist">
                                <li class="nav-item">
                                    <a class="nav-link active" id="login-tab" href="javascript:void(0)" role="tab">{{$t('DEFAULT.OJ_MESSAGE_LOGIN')}}</a>
                                </li>
                                <li class="nav-item">
                                    <a class="nav-link " id="register-tab"
                                       href="registerpage.html" role="tab">{{$t('DEFAULT.OJ_MESSAGE_REGISTER')}}</a>
                                </li>
                                <div class="nav-tabs-indicator" id="nav-tabs-indicator"></div>
                            </ul>
                        </div>
                        <div class="tab-content" id="accountTabContent">
                            <div class="tab-pane fade show active" role="tabpanel">
                                <form class="needs-validation" id="login_form" @submit.prevent="loginsubmit()">
                                    <div class="card-body">
                                        <div class="form-group bmd-form-group"
                                             v-bind:class="{ 'is-filled': inputs.account != '', 'is-focused': current_focus == 'account' && has_focus }">
                                            <label for="account" class="bmd-label-floating">{{$t('DEFAULT.OJ_MESSAGE_ACCOUNT')}}</label>
                                            <input type="text" name="account" class="form-control" id="account"
                                                   v-model="inputs.account" required @focus="is_focus('account')"
                                                   @blur="is_blur();check_account()" @keyup="check_account()"/>
                                            <span v-bind:style="{display: 'inline-block',float: 'right',color:error_message.account==$t('WARN.OJ_MESSAGE_ACCEPTABLE')? 'green':'', visibility: (has_focus && current_focus == 'account') || inputs.account.length > 0? '': 'hidden'}"
                                                  class="invalid-feedback"><strong>{{error_message.account}}</strong></span>
                                            <div style="clear: both"></div>
                                        </div>
                                        <div class="form-group bmd-form-group"
                                             v-bind:class="{ 'is-filled': inputs.password != '', 'is-focused': current_focus == 'password' && has_focus }">
                                            <label for="password" class="bmd-label-floating">{{$t('DEFAULT.OJ_MESSAGE_PASSWORD')}}</label>
                                            <input type="password" class="form-control" id="password" autocomplete="off"
                                                   required="required" v-model="inputs.password"
                                                   @focus="is_focus('password')" @blur="is_blur();check_password()"
                                                   @keyup="check_password()">
                                            <span v-bind:style="{display: 'inline-block',float: 'right',color:error_message.password==$t('WARN.OJ_MESSAGE_ACCEPTABLE')? 'green':'', visibility: (has_focus && current_focus == 'password') || inputs.password.length > 0? '': 'hidden'}"
                                                  class="invalid-feedback"><strong>{{error_message.password}}</strong></span>
                                            <div style="clear: both"></div>
                                        </div>
                                        <template v-if="is_verify_vcode == true">
                                            <div style="float: left;width: 65%" class="form-group bmd-form-group"
                                                 v-bind:class="{ 'is-filled': inputs.vcode != '', 'is-focused': current_focus == 'vcode' && has_focus }">
                                                <label for="vcode" class="bmd-label-floating">{{$t('DEFAULT.OJ_MESSAGE_VERIFICATION_CODE')}}</label>
                                                <input type="password" class="form-control" id="vcode"
                                                       autocomplete="off"
                                                       required="required" v-model="inputs.vcode"
                                                       @focus="is_focus('vcode')" @blur="is_blur();check_vcode()"
                                                       @keyup="check_vcode()">
                                                <span v-bind:style="{display: 'inline-block',float: 'right',color:error_message.vcode==$t('WARN.OJ_MESSAGE_ACCEPTABLE')? 'green':'', visibility: (has_focus && current_focus == 'vcode') || inputs.vcode.length > 0? '': 'hidden'}"
                                                      class="invalid-feedback"><strong>{{error_message.vcode}}</strong></span>
                                                <div style="clear: both"></div>
                                            </div>
                                            <!---->
                                            <div style="float: right;width: 30%;margin-top: 21px;margin-right: 10px;">
                                                <img ref="vcode_img" @click="get_vcode_img()" style="cursor: pointer"/>
                                            </div>
                                            <div style="clear: both"></div>
                                        </template>

                                        <div class="form-group bmd-form-group">
                                            <div class="checkbox">
                                                <label for="remember" class="is-filled">
                                                    <input class="form-control" type="checkbox" name="remember"
                                                           id="remember" value="rememberme" v-model="inputs.rememberme">
                                                    <span class="checkbox-decorator">
                                                        <span class="check"></span>
                                                    </span>
                                                    <span>{{$t('DEFAULT.OJ_MESSAGE_REMEMBER_ME')}}</span>
                                                </label>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="card-footer text-right">
                                        <a href="javascript:void(0)">
                                            <button type="button" class="btn btn-secondary">
                                                {{$t('DEFAULT.OJ_MESSAGE_FORGET_PASSWORD')}}
                                            </button>
                                        </a>
                                        <button type="submit" class="btn btn-danger">
                                            {{$t('DEFAULT.OJ_MESSAGE_LOGIN')}}
                                        </button>
                                        <!--<div @click="test">55</div>-->
                                    </div>
                                </form>
                                <!--<button @click="submit_img" type="button" class="btn btn-secondary">&lt;!&ndash;&ndash;&gt;-->
                                <!--&lt;!&ndash;&ndash;&gt;<span>上传图片</span>-->
                                <!--</button>-->
                                <!--<input @change="test" type="file" name="file" accept="image/*" class="el-upload__input"-->
                                <!--ref="imgs">-->
                            </div>
                        </div>
                        <!--<div class="text-center card-bottom socialite-section">{{test}}</div>-->
                        <!--<img src="http://localhost:8081/OnlineJudge/vcode" ref="codeimg"/>-->
                    </div>
                </div>
            </div>
        </div>
        <template v-if="model_open == true">
            <div class="modal fade" id="login_error" tabindex="-1" role="dialog"
                 aria-labelledby="login_error_title" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered" role="document">
                    <div class="modal-content">
                        <div class="modal-body">
                            <h3 style="color: #ff3f4c">
                                {{$t('WARN.OJ_MESSAGE_UNSUCCESSFULLY_LOGIN')}}
                            </h3>
                            <br>
                            <p>{{login_result.login_info}}</p>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-secondary"
                                    data-dismiss="modal" @click="close_model()">
                                OK
                            </button>
                        </div>
                    </div>

                </div>
            </div>
        </template>
        <footers></footers>
    </div>
</template>
<script>
    import {hex_md5} from '@/assets/js/md5.js'
    import NavBar from "@/components/navbar/navbar";
    import fun from '@/assets/js/OJFunction'
    import Footers from "@/components/footer/footer";

    export default {
        name: "loginpage",
        components: {Footers, NavBar},
        data() {
            return {
                inputs: {
                    account: "",
                    password: "",
                    rememberme: "",
                    vcode: "",
                    csrf: ""
                },
                current_focus: '',
                has_focus: false,
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: "",
                    belong: "account"
                },
                login_result: {
                    is_login: false,
                    has_error: false,
                    login_info: ""
                },
                error_message: {
                    account: "　",
                    password: "　",
                    vcode: "　"
                },
                is_verify_vcode: false,
                model_open: false
            }
        },
        methods: {
            submit_img: function () {
                this.$refs.imgs.click();
            },
            handleSuccess(response, file, fileList) {
                response;
                file;
                fileList;
                this.$success("上传成功");
            },
            handleError() {
                this.$error("上传失败,请重新上传图片!");
            },
            handleBeforeUpload(file) {
                const isImage = file.type.includes("image");
                if (!isImage) {
                    this.$message.error("上传文件类型必须是图片!");
                }
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isLt2M) {
                    this.$message.error("上传图片大小不能超过 2MB!");
                }
                return isImage && isLt2M;
            },
            handleProgress(event, file, fileList) {
                event;
                file;
                fileList;
                this.loading = true;  //  上传时执行loading事件
            },
            test: function () {
                // alert("hello");
                var fromdata = new FormData();
                fromdata.append("file", this.$refs.imgs.files[0]);
                console.log(fromdata.get("file"));
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/updata',
                    fromdata, {contentType: false, processData: false}).then(function (res) {
                    // alert(res);r
                    res;
                }, function () {
                    console.log('请求失败处理');
                });
            },
            loginsubmit: function () {
                // console.log(this.inputs);
                this.model_open = false;
                this.login_result.login_info = "";
                this.login_result.is_login = true;
                this.login_result.has_error = false;
                this.inputs.password = hex_md5(this.inputs.password);
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/account/login',
                    {
                        vcode: this.inputs.vcode,
                        account: this.inputs.account,
                        password: this.inputs.password,
                        rememberme: this.inputs.rememberme
                    }, {
                        emulateJSON: true,
                        headers: {
                            OJ_ACCESS_TOKEN: this.page_setting.access_token,
                            OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                        }
                    }).then(res => {
                    // success callback
                    this.login_result.is_login = false;
                    // alert("4")
                    // console.log(res);
                    var error_type = res.body.OJ_ERROR_TYPE;
                    if (error_type != this.GLOBAL.OJ_ERROR_NO_ERROR) {
                        this.login_result.has_error = true;
                        var error_times = Number(res.body.OJ_LOGIN_ERROR_TIMES);
                        //alert(error_times >= 10);
                        if (error_times >= this.GLOBAL.OJ_DEFAULT_NEED_VCODE_TIMES) this.get_vcode_img();
                        this.inputs.password = "";
                    }
                    if (error_type == this.GLOBAL.OJ_ERROR_IP_BE_BANNED) {
                        this.login_result.login_info = this.$t('WARN.OJ_MESSAGE_IP_BE_BANNED');
                    } else if (error_type == this.GLOBAL.OJ_ERROR_WRONG_VCODE) {
                        this.inputs.vcode = "";
                        $("#vcode").focus();
                        this.error_message.vcode = this.$t('WARN.OJ_MESSAGE_WRONG_VCODE');
                        this.get_vcode_img();
                    } else if (error_type == this.GLOBAL.OJ_ERROR_ACCOUNT_BE_BANNED) {
                        this.login_result.login_info = this.$t('WARN.OJ_MESSAGE_ACCOUNT_BE_BANNED');
                    } else if (error_type == this.GLOBAL.OJ_ERROR_NON_EXISTENT_ACCOUNT) {
                        $("#account").focus();
                        this.error_message.account = this.$t('WARN.OJ_MESSAGE_NON_EXISTENT_ACCOUNT');
                        document.getElementById('account').setCustomValidity(this.$t('WARN.OJ_MESSAGE_NON_EXISTENT_ACCOUNT'));
                    } else if (error_type == this.GLOBAL.OJ_ERROR_REPETED_ACCOUNT) {
                        $("#account").focus();
                        this.error_message.account = this.$t('WARN.OJ_MESSAGE_REPETED_ACCOUNT');
                        document.getElementById('account').setCustomValidity(this.$t('WARN.OJ_MESSAGE_REPETED_ACCOUNT'));
                    } else if (error_type == this.GLOBAL.OJ_ERROR_ACCOUNT_BE_BANNED) {
                        this.login_result.login_info = this.$t('WARN.OJ_MESSAGE_ACCOUNT_BE_BANNED');
                    } else if (error_type == this.GLOBAL.OJ_ERROR_WRONG_PASSWORD) {
                        $("#password").focus();
                        this.error_message.password = this.$t('WARN.OJ_MESSAGE_WRONG_PASSWORD');
                        document.getElementById('password').setCustomValidity(this.$t('WARN.OJ_MESSAGE_WRONG_PASSWORD'));
                    } else if (error_type != this.GLOBAL.OJ_ERROR_NO_ERROR) {
                        this.login_result.login_info = this.$t('WARN.OJ_MESSAGE_BACK_JUMP');
                    }
                    if (this.login_result.login_info.length > 0) {
                        this.model_open = true;
                        setTimeout(function () {
                            $('#login_error').modal({
                                backdrop: 'static'
                            })
                        }, 100)
                    }

                    var remember_me = res.body.OJ_USER_INFO.saved;
                    // alert(this.login_result.has_error)
                    if (this.login_result.has_error == false) {

                        var access_token = res.headers.map['oj_access_token'][0];
                        var refresh_token = res.headers.map['oj_refresh_token'][0];

                        // alert(access_token);
                        // alert(remember_me)
                        if (remember_me) {
                            // alert(1);
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                        } else {
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                            // var zz = getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");

                        }
                        // alert()
                        window.history.go(-1);
                    }
                }, err => {
                    console.log(err.body);
                });
            },
            is_focus: function (num) {
                this.current_focus = num;
                this.has_focus = true;
            },
            is_blur: function () {
                this.has_focus = false;
            },
            check_account: function () {
                var data = document.getElementById('account');
                this.error_message.account = "　";
                data.setCustomValidity("");
            },
            check_password: function () {
                var data = document.getElementById('password');
                this.error_message.password = "　";
                data.setCustomValidity("");
            },
            check_vcode: function () {
                var data = document.getElementById('vcode');
                this.error_message.vcode = "　";
                data.setCustomValidity("");
            },
            get_vcode_img: function () {
                this.is_verify_vcode = true;
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/vcode',
                    {}, {responseType: 'arraybuffer'}).then(function (res) {
                    var url = 'data:image/png;base64,' + btoa(new Uint8Array(res.data).reduce((data, byte) => data + String.fromCharCode(byte), ''));
                    this.$refs.vcode_img.src = url;
                }, function () {
                    console.log('请求失败处理');
                });
            },
            close_model: function () {
                var _this = this;
                setTimeout(function () {
                    _this.model_open = false;
                    _this.login_result.login_info = "";
                }, 300)
            },
        },
        created() {
            this.is_verify_vcode = false;
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");
            // alert(access_token);
            // alert(this.page_setting.refresh_token);
            var lang = localStorage.getItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang");
            if (lang != null) {
                this.page_setting.language = lang;
            }
            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/loginpage',
                {
                    language: this.page_setting.language
                }, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                // console.log(res.headers.map['555'][0]);
                var error_type = res.body.OJ_ERROR_TYPE;
                if (error_type == this.GLOBAL.OJ_ERROR_HAS_LOGIN) {
                    window.history.go(-1);
                    return;
                }
                fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_VISITOR);
                this.inputs.csrf = res.body.OJ_LOGIN_CSRF;

                lang = res.body.OJ_LANGUAGE_TYPE;
                localStorage.setItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang", lang);
                this.$i18n.locale = lang;

                var error_times = Number(res.body.OJ_LOGIN_ERROR_TIMES);
                if (error_times >= this.GLOBAL.OJ_DEFAULT_NEED_VCODE_TIMES) this.get_vcode_img();
            }, function () {
                console.log('请求失败处理');
            });
        }
    }
</script>
<style lang="scss" scoped>
    #loginpage {
        .modal-body {
            h3 {
                font-weight: bold;
                font-family: 'Roboto-slab';
                margin-bottom: 0.5rem;
            }
            p {
                /*color: rgba(0, 0, 0, 0.54);*/
                margin-bottom: 0.5rem;
                margin-top: 0.5rem;
                font-weight: bold;
            }
        }
        .mundb-standard-container {
            flex-shrink: 0;
            flex-grow: 1;
        }
        .card {
            margin-bottom: 20vh;
            overflow: hidden;
            display: block;
            box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 30px;
            border-radius: 4px;
            transition: .2s ease-out .0s;
            color: #7a8e97;
            background: #fff;
            position: relative;
            border: 1px solid rgba(0, 0, 0, 0.15);
        }
        .card-body {
            .form-group {
                margin-bottom: 0px;
            }
        }
        .card .card-header {
            padding: 0;
        }
        #accountTab {
            position: relative;
        }
        .card .card-header > ul {
            margin: 0;
        }
        .card .card-header > ul .nav-link {
            padding: 1rem;
            border: none !important;
            font-weight: bold;
        }
        .card .card-header .nav-tabs .nav-link.active {
            color: #ff4081;
        }
        .card-bottom {
            background-color: rgb(244, 244, 244);
            padding: 10px;
        }
        .nav-tabs-material .nav-tabs-indicator {
            background-color: #ff4081;
            bottom: -1px;
            display: block;
            width: 50%;
            height: .15rem;
            position: absolute;
            transition: .2s ease-out .0s;
        }
        form {
            margin-bottom: 0;
        }
        .checkbox {
            margin-top: 1rem;
        }
        .card-footer {
            border: none;
        }
    }
</style>